import logo from './logo.svg';
import { Container, Navbar, Nav } from 'react-bootstrap'; //  import
import './App.css';
import bg from './img/bg.png';  //  import
import { lazy, Suspense, createContext, useEffect, useState } from 'react';
import data from './data.js'; // data.js import
import { Routes, Route, Link, useNavigate, Outlet } from 'react-router-dom'  //  import
// import Detail from './routes/Detail.js'; // deatil.js import
import axios from 'axios'
import $ from 'jquery';
// import Cart from './routes/Cart.js';
import { useQuery } from '@tanstack/react-query';

export let Context1 = createContext()  // state  export

// lazy 
const Detail = lazy(()=>import('./routes/Detail.js'));
const Cart = lazy(()=>import('./routes/Cart.js'));

function App() {

  //  state
  let [, ] = useState([10, 11, 12]);  

  // state 
  let [shoes, setShoes] = useState(data);

  //   
  let [count, setCount] = useState(0);

  // useNavigate  
  let navigate = useNavigate();

  // useQuery
  let result = useQuery([''], async () => {
    try {
      const response = await axios.get('https://codingapple1.github.io/userdata.json');
      return response.data || {}; //      
    } catch (error) {
      console.error('    :', error);
      return {}; //      
    }
  });


  //    array  
  useEffect(()=>{
    if (localStorage.getItem('watched') == null) {
      localStorage.setItem('watched', JSON.stringify([]));
    }
  }, [])


  return (
    <div className="App">

      {/* navbar */}
      <Navbar bg="light" data-bs-theme="light">
        <Container>
          <Navbar.Brand href="#home"></Navbar.Brand>
          <Nav className="me-auto">
            {/* navigate  */}
            <Nav.Link onClick={() => { navigate('/') }}>Home</Nav.Link>
            <Nav.Link onClick={() => { navigate('/cart') }}>Cart</Nav.Link>
          </Nav>
          <Nav className="ms-auto">
            { result.isLoading && '' }
            { result.error && '' }
            { result.data && result.data.name }
          </Nav>
        </Container>
      </Navbar>

      {/*   */}
      {/* <Link to="/"></Link>
      <Link to="/detail"></Link> */}

      {/*  */}
      {/*     */}
      <Suspense fallback={<div></div>}>
        <Routes>

          {/*    */}
          <Route path='/' element={
            <>
              {/* main-bg */}
              <div className='main-bg' style={{ backgroundImage: `url(${bg})`}}></div>

              {/*    */}
              <div className='container'>
                <div className='row'>

                  {/*    */}
                  {shoes.map(function (elem, index) {
                    return <Card elem={elem} index={index} key={index}></Card>  // props 
                  })}

                </div>
              </div>

              {/*     */}
              <button onClick={() => {
                <div className='load'>.</div>
                if (count == 0) {
                  axios.get('https://codingapple1.github.io/shop/data2.json')
                    .then((result) => {
                      let copy = [...shoes, ...result.data];
                      setShoes(copy);
                      $('.load').fadeOut();
                    })
                    .catch(() => {
                      console.log('');
                      $('.load').fadeOut();
                    });
                }
                if (count == 1) {
                  axios.get('https://codingapple1.github.io/shop/data3.json')
                    .then((result) => {
                      let copy = [...shoes, ...result.data];
                      setShoes(copy);
                    })
                    .catch(() => {
                      console.log('');
                    });
                }
                if (count == 2) {
                  alert('');
                }
                setCount(count + 1);
              }}></button>

            </>
          }></Route>

          {/* detail   */}
          {/* url  */}
          <Route path='/detail/:id' element={
            // context API 
            
              <Context1.Provider value={{}}>  
                <Detail shoes={shoes}></Detail>
              </Context1.Provider>
            
          }></Route>

          {/* nested routes */}
          <Route path='/about' element={<About></About>}>
            <Route path='member' element={<div></div>}></Route>
            <Route path='location' element={<div></div>}></Route>
          </Route>

          {/* event   */}
          <Route path='/event' element={<Event></Event>}>
            <Route path='one' element={<div>   </div>}></Route>
            <Route path='two' element={<div> </div>}></Route>
          </Route>

          <Route path='/cart' element={<Cart></Cart>}></Route>

          {/*     */}
          <Route path='*' element={<div></div>}></Route>
        </Routes>
      </Suspense>

    </div>
  );
}

// Event 
function Event() {
  return (
    <div>
      <h4> </h4>
      <Outlet></Outlet>
    </div>
  )
}

// About 
function About() {
  return (
    <div>
      <h4></h4>

      {/* Outlet  */}
      <Outlet></Outlet>
    </div>
  )
}

// Card  (props )
function Card(props) {
  return (
    <div className='col-md-4' key={props.index}>
      <Link to={`/detail/${props.index}`}>
        <img src={'https://codingapple1.github.io/shop/shoes' + parseInt((props.index) + 1) + '.jpg'} width="80%"></img>
        <h4>{props.elem.title}</h4>
        <p>{props.elem.price}</p>
      </Link>
    </div>
  )
}

export default App;
